<style scoped lang="less">
</style>
<template>
  <div class="pendingRecord content-layout">
    <div class="content-header">
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="a" label="病理号"></el-table-column>
        <el-table-column prop="b" label="姓名"></el-table-column>
        <el-table-column prop="c" label="性别"></el-table-column>
        <el-table-column prop="d" label="年龄"></el-table-column>
        <el-table-column prop="e" label="送检部位"></el-table-column>
        <el-table-column prop="f" label="送检类型"></el-table-column>
        <el-table-column prop="g" label="送检时间" width="150"></el-table-column>
        <el-table-column prop="h" label="流程状态"></el-table-column>
        <el-table-column label="操作" width="150">
          <template scope="scope">
            <el-button type="text" size="small" @click="lookRecord(scope.$index, tableData)">查看</el-button>
            <el-button type="text" size="small" @click="withdraw(scope.$index, tableData)">撤回</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <look-record v-show="showRecord" :recordData="recordData"  @getChildMessage="closeRecord"></look-record>
    </transition>
  </div>
</template>
<script>
  import headerSearch from '../../../public/header-search'
  import lookRecord from '../../../public/lookRecord'
  import searchDataModule from '../../../../api/searchDataModule'
  import searchInitDefault from '../../../../api/searchInitDefault'
  export default {
    name: '',
    data(){
      return {
        seachData: {
          starttime: searchDataModule.starttime,
          endtime: searchDataModule.endtime,
          xbval:searchDataModule.xbval,
          xb:searchDataModule.xb,
          ysval: searchDataModule.ysval,
          ys: searchDataModule.ys,
          seachInfo: searchDataModule.seachInfo
        },
        tableData: [
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待诊断'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待诊断'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待诊断'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'}
        ],
        currentPage: 1,
        recordData: {},//病历信息
        showRecord: false,//病历信息显示隐藏
      }
    },
    props: [''],
    components: {
      headerSearch,
      lookRecord
    },
    mounted(){
      searchInitDefault(searchDataModule);
    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },

      seachFn(val){
        console.log(this.seachData)
      },
      lookRecord(i,arr){
        this.showRecord = true;
        this.recordData = {
          radio: '1'
        }
      },
      closeRecord(val) {
        //evtValue 关闭  是子组件传过来的值关闭病历信息组件
        this.showRecord = val
      },
      withdraw(i,arr){
        this.$confirm('此操作将撤回病历, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
//          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '撤回成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消撤回'
          });
        });
      },
    }
  }
</script>







